import React, { useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Grid,
  TextField,
  Button,
  Switch,
  FormControlLabel,
  Divider,
  Alert,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';
import { Delete as DeleteIcon, Add as AddIcon, Logout as LogoutIcon } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

export const Settings = () => {
  const navigate = useNavigate();
  const [openLogoutDialog, setOpenLogoutDialog] = useState(false);
  const [profile, setProfile] = useState({
    displayName: '',
    email: 'personal@gmail.com',
  });

  const [notifications, setNotifications] = useState({
    emailNotifications: true,
    deleteDangerousFiles: true,
  });

  const [connectedEmails, setConnectedEmails] = useState([
    { email: 'company@gmail.com', type: ' ' },
    { email: 'personal@gmail.com', type: ' ' },
  ]);

  const [newEmail, setNewEmail] = useState('');
  const [showSuccess, setShowSuccess] = useState(false);

  const handleProfileUpdate = () => {
    // TODO: API   
    setShowSuccess(true);
    setTimeout(() => setShowSuccess(false), 3000);
  };

  const handleNotificationChange = (setting) => (event) => {
    setNotifications({
      ...notifications,
      [setting]: event.target.checked,
    });
  };

  const handleEmailAdd = () => {
    if (newEmail) {
      setConnectedEmails([...connectedEmails, { email: newEmail, type: ' ' }]);
      setNewEmail('');
    }
  };

  const handleEmailDelete = (email) => {
    setConnectedEmails(connectedEmails.filter(e => e.email !== email));
  };

  const handleLogoutClick = () => {
    setOpenLogoutDialog(true);
  };

  const handleLogoutConfirm = () => {
    // TODO:   
    setOpenLogoutDialog(false);
    navigate('/');
  };

  const handleLogoutCancel = () => {
    setOpenLogoutDialog(false);
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">   </Typography>
        <Button
          variant="outlined"
          color="error"
          startIcon={<LogoutIcon />}
          onClick={handleLogoutClick}
          sx={{ borderRadius: '20px' }}
        >
          
        </Button>
      </Box>

      {/*    */}
      <Dialog
        open={openLogoutDialog}
        onClose={handleLogoutCancel}
        sx={{
          '& .MuiDialog-paper': {
            borderRadius: '12px',
            minWidth: '300px',
          },
        }}
      >
        <DialogTitle sx={{ pb: 1 }}>
           
        </DialogTitle>
        <DialogContent>
          <Typography>
              ?
          </Typography>
        </DialogContent>
        <DialogActions sx={{ p: 2, pt: 0 }}>
          <Button 
            onClick={handleLogoutCancel}
            sx={{ 
              borderRadius: '20px',
              px: 3,
              color: 'text.secondary'
            }}
          >
            
          </Button>
          <Button 
            onClick={handleLogoutConfirm}
            variant="contained"
            color="error"
            sx={{ 
              borderRadius: '20px',
              px: 3
            }}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {showSuccess && (
        <Alert severity="success" sx={{ mb: 2 }}>
            .
        </Alert>
      )}

      <Grid container spacing={3}>
        {/*   */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>   </Typography>
              <Box component="form" sx={{ mt: 2 }}>
                <TextField fullWidth label="" value={profile.displayName}
                    onChange={(e) => setProfile({ ...profile, displayName: e.target.value })} margin="normal" />
                <TextField fullWidth label=" " value={profile.email}
                    onChange={(e) => setProfile({ ...profile, email: e.target.value })} margin="normal" />
                <Button variant="contained" color="primary" onClick={handleProfileUpdate} sx={{ mt: 2 }}>
                   
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/*   */}
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>   </Typography>
              <List>
                <ListItem>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={notifications.emailNotifications}
                        onChange={handleNotificationChange('emailNotifications')}
                      />
                    }
                    label="  "
                  />
                </ListItem>
                <ListItem>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={notifications.deleteDangerousFiles}
                        onChange={handleNotificationChange('deleteDangerousFiles')}
                      />
                    }
                    label="   "
                  />
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Grid>

        {/*    */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>    </Typography>
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle2" color="text.secondary" gutterBottom>
                    
                </Typography>
                <List>
                  {connectedEmails.map((email) => (
                    <ListItem key={email.email}>
                      <ListItemText
                        primary={email.email}
                        secondary={email.type}
                      />
                      <ListItemSecondaryAction>
                        <IconButton
                          edge="end"
                          aria-label="delete"
                          onClick={() => handleEmailDelete(email.email)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </ListItemSecondaryAction>
                    </ListItem>
                  ))}
                </List>
              </Box>
              <Divider sx={{ my: 2 }} />
              <Box sx={{ display: 'flex', gap: 1, alignItems: 'flex-start' }}>
                <TextField label="  " value={newEmail}
                    onChange={(e) => setNewEmail(e.target.value)} sx={{ flexGrow: 1 }} />
                <Button variant="contained" startIcon={<AddIcon />} onClick={handleEmailAdd} sx={{ mt: 1 }}>
                  
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
}; 